<template>
  <div>
    <!-- header begin -->
    <headerBar :header-data="headerData" />
    <!-- header end -->

    <div ref="testref" class="container-tab">
      <van-sticky class="tabs-sticky">
        <!-- 主体信息 begin -->
        <van-tabs swipeable animated sticky @click="onClick">
          <van-tab v-for="(items,index) in tabs" :key="index" :title=" items">
            <wybm v-if="index==0" ref="mychilds" />
            <bmls v-if="index==1" ref="mychild" />
          </van-tab>
        </van-tabs>
      <!-- 主体信息 end -->
      </van-sticky>
    </div>
    <FooterNav />
    <searchMeun @goSearchAction="searchXmxx($event)" @reSet="reSet">
      <van-field v-model="search.xmmc" label="项目名称" placeholder="请输入项目名称" input-align="right" clearable />
      <van-field v-model="search.xmbh" label="项目编号" placeholder="请输入项目编号" input-align="right" clearable />
      <van-cell v-model="showCgfs" title="采购方式" is-link @click="showcgfsPopup()" />
      <van-cell v-model="date" title="开标时间" is-link @click="showCalendars()" />
    </searchMeun>
    <searchPick
      :pick-title="searchPick.PickTitle"
      :pick-columns="searchPick.PickColumns"
      @confirmClick="confirmPick($event)"
    />
    <searchCalendar
      :calendar-name="searchCalendar.calendarName"
      @confirmCalendar="confirmCalendar($event)"
    />
  </div>
</template>

<script land="script" scoped>
import FooterNav from '@/components/footerNav/footerNav.vue'
import constants from '../../../common/js/constants'
import { mapState } from 'vuex'
import wybm from './bm/wybm' // 我要报名
import bmls from './bm/bmls' // 报名历史
// import { pageRouter } from '@/common/js/mixin.js'
import headerBar from '@/components/header/headerBar.vue'
import searchMeun from '@/components/searchComponents/searchMeun.vue'
import searchPick from '@/components/searchComponents/searchPick.vue'
import searchCalendar from '@/components/searchComponents/searchCalendar.vue'

export default {
  name: 'Wybm',
  components: {
    FooterNav,
    wybm,
    bmls,
    headerBar,
    searchMeun,
    searchPick,
    searchCalendar
  },
  // mixins: [pageRouter],
  data() {
    return {
      ind: null, // 点击事件
      pageno: 1,
      headerData: {
        title: '我要报名',
        leftUrl: '/MhHome',
        leftText: ' ',
        showSearch: true
      },
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      search: {
        xmmc: '',
        xmbh: '',
        cgfs: '',
        date: '',
        kssj: '',
        jssj: ''
      },
      searchPick: {
        PickTitle: '',
        PickColumns: ''
      },
      searchCalendar: {
        calendarName: ''
      },
      tabs: ['我要报名', '报名历史'],
      query: '', // 搜索
      pageName: '',
      date: '请选择开标-结束时间',
      showCgfs: '请选择采购方式'
    }
  },
  computed: {
    ...mapState({
      isLogin: state => state.mh.mhisLogin,
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    this.$store.commit('router/SET_Foot_Num', 1)
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    this.getCGFS()
  },

  methods: {
    reSet() {
      this.search = {
        xmmc: '',
        xmbh: '',
        cgfs: '',
        date: '',
        kssj: '',
        jssj: ''
      }
      this.showCgfs = '请选择采购方式'
      this.date = '请选择开标-结束时间'
    },
    // 搜索数据
    onQueryChange(query) {
      this.query = query
    },
    onClick(data, ref) {
      this.pageName = ref
    },
    // 获取采购方式
    getCGFS() {
      this.$store
        .dispatch('gyswybm/getCgfsAction')
        .then(res => {
          const maps = []
          for (var key in res) {
            console.log(key, res[key])
            maps.push({ 'value': key, 'label': res[key] })
          }
          this.searchPick.PickColumns = maps
        })
        .catch((error) => {
          // this.pageStyles = 3
          this.$toast.fail(error)
          // this.loading = false
        })
    },
    // 搜索点击事件
    searchXmxx(type) {
      this.$bus.$emit('showMeun', false)
      const query = { xmmc: this.search.xmmc, xmbh: this.search.xmbh, cgfs: this.search.cgfs === '请选择采购方式' ? '' : this.search.cgfs, kssj: this.search.kssj, jssj: this.search.jssj }
      const child = this.pageName
      console.log('chchchchchch', child)
      if (child === '报名历史') {
        this.$bus.$emit('searchChilde', query) // bus触发事件
      } else {
        this.$bus.$emit('changeChilde', query) // bus触发事件
      }
    },

    // 展示采购方式
    showcgfsPopup() {
      // this.searchPick.PickShow = true
      this.$bus.$emit('showPick', true)
      this.searchPick.PickTitle = '选择采购方式'
    },
    // 采购方式回调
    confirmPick(pick) {
      this.showCgfs = pick.label
      this.search.cgfs = pick.value
      console.log('pick==========', pick.value)
      console.log('pick==========', pick.label)
    },
    // 展示日历
    showCalendars() {
      console.log('showCalendars==========')
      this.$bus.$emit('showCalendar', true)
      this.searchCalendar.calendarName = '请选择开标-结束时间'
      // this.searchCalendar.showCalendar = true
    },
    // 日历返回值
    confirmCalendar(date) {
      const [start, end] = date
      this.show2 = false
      this.kbsjbegin = this.formatDate8(start)
      this.kbsjend = this.formatDate8(end)
      this.date = `${this.formatDateShow(start)}-${this.formatDateShow(end)}`
      this.search.kssj = parseInt(this.kbsjbegin)
      this.search.jssj = parseInt(this.kbsjend)
    },
    formatDate8(date) {
      var month = date.getMonth() + 1
      var day = date.getDate()
      if (month < 10) {
        month = '0' + month
      }
      if (day < 10) {
        day = '0' + day
      }
      return `${date.getFullYear()}${month}${day}`
    },
    formatDateShow(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    },
    goXmfb() {
      this.$router.push({
        name: 'Xmfb'
      })
    }
  }

}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
