<template>
  <div class="container-mini">
    <common-list
      ref="didaList"
      class="common-list"
      :post-url="list.postUrl"
      :req-data="list.reqData"
      :data-name="list.dataName"
      @returnData="getPrizeLog($event)"
    >
      <div class="content">
        <div class="common-main box">
          <div class="common-list">

            <div
              v-for="(item, index) in list.items"
              :key="index"
              :class="{ focus: ind === index }"
              class="list"
              @click="itemClick(index)"
            >
              <h3><div class="tit">{{ item.xmmc }}</div><span class="label">{{ CGFS[item.cgfs] }}</span></h3>

              <!-- <div class="info">
                  <span class="label green"> {{ XMZT[item.xmzt] }}</span>
                </div> -->
              <div class="info">
                <span>
                  <i class="iconfont iconbianma" />
                  {{ item.zbbh }}
                </span>
                <span>
                  <i class="iconfont iconlianxiren1" />
                  {{ item.lxr }}
                </span>
                <span>
                  <i class="iconfont iconzhuangtai" />
                  {{ XMZT[item.xmzt] }}
                </span>
                <!-- <span>
                    <i class="iconfont iconshouji" />
                    {{ item.lxdh }}
                  </span> -->
              </div>
              <div class="info">
                <span v-if="item.kbsj">
                  开标时间
                  {{ item.kbsj | formatTime }}
                </span>
                <span class="orange">
                  报名倒计时
                  {{ item.bmsysj | formatDayTime }}
                </span>
              </div>
            </div>

          </div>
        </div>
      </div>
    </common-list>
  </div>
</template>

<script land="script" scoped>
// import defaultList from '@/components/list/defaultList.vue'
import commonList from '@/components/list/commonList.vue'
import constants from '@/common/js/constants'
// import errorPage from '../../../components/result-page/index'
import { mapState } from 'vuex'
// import { getWybmListAction } from '../../store/module/wybm'
export default {
  components: {
    commonList
    // xyScroll,
    // defaultList
    // SearchBox
  },
  props: {
    bmztBumber: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      ind: null, // 点击事件
      pageno: 1,
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      }
    }
  },
  computed: {
    ...mapState({
      isLogin: state => state.gysuser.gysLogin,
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    // 不会引起DOM变化的数据在此定义
    this.$bus.$on('changeChilde', (msg) => { // bus接收事件
      console.log('changeChilde==============', msg)
      this.$refs.didaList.onRefresh(msg)
    })
  },
  beforeDestroy() {
    this.$bus.$off('changeChilde')
  },
  mounted() {
    this.getdbsxlist('')
  },
  methods: {
    getPrizeLog(data) {
      console.info('data=====AAAA====', data)
      this.list.items = data
    },
    getdbsxlist() {
      this.list.postUrl = 'gyswybm/getWybmListAction'
      this.list.reqData = {
        id: this.userInfo.id,
        zzid: this.userInfo.zzid,
        bmzt: 0
      }
      this.list.dataName = 'xmxxList'
      // this.list.noDataText = '暂无更多待办事项'
    },

    /* list点击事件*/
    itemClick: function(index) {
      this.$store.dispatch('gyswybm/setWybmlistItemAction', this.list.items[index])
      this.ind = index
      var list = []
      this.$store.commit('router/SET_KEEP_ALIVE', list)
      if (this.list.items[index].gysxzfs === 1 || (this.list.items[index].cgfs === '00380002' && this.list.items[index].xmxh == null)) {
        this.$router.push({
          name: 'Xmfb',
          params: { 'state': 1 }
        })
      } else {
        if (this.list.items[index].sfdlbb === 1) {
          if (this.list.items[index].sftyyq === 1) {
            this.$router.push({
              name: 'Xmfb',
              params: { 'state': 1 }
            })
          } else {
            this.$router.push({
              name: 'Yqhlist'
            })
          }
        } else {
          this.$router.push({
            name: 'Xmfb',
            params: { 'state': 1 }
          })
        }
      }
    },
    showPopup() {
      this.show = true
    },
    showcgfsPopup() {
      this.show1 = true
    },
    showcalendar() {
      this.show2 = true
    },
    formatDate8(date) {
      var month = date.getMonth() + 1
      var day = date.getDate()
      if (month < 10) {
        month = '0' + month
      }
      if (day < 10) {
        day = '0' + day
      }
      return `${date.getFullYear()}${month}${day}`
    },
    formatDateShow(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    },
    onConfirm(date) {
      const [start, end] = date
      this.show2 = false
      this.kbsjbegin = this.formatDate8(start)
      this.kbsjend = this.formatDate8(end)
      this.date = `${this.formatDateShow(start)}-${this.formatDateShow(end)}`
    },
    onCgfsCancel() {
      this.show1 = false
    },
    onCgfsConfirm(pick) {
      this.show1 = false
      this.cgfs = pick.value
      this.showCgfs = pick.label
    },
    goXmfb() {
      this.$router.push({
        name: 'Xmfb'
      })
    }
  }

}
</script>

<style lang="css" scoped>

</style>
